
"use client";

import { useState, useMemo, type Key, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { DropdownMenu, DropdownMenuContent, DropdownMenuCheckboxItem, DropdownMenuLabel, DropdownMenuSeparator, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Eye, Search as SearchIcon, Filter as FilterIcon, ArrowUpDown, ChevronLeft, ChevronRight, PencilLine, Trash2, ShoppingCart } from "lucide-react";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { cn } from '@/lib/utils';

interface Order {
  id: string;
  customerName: string;
  customerEmail: string; // Used for avatar generation
  orderDate: string;
  totalAmount: number;
  status: "Pending" | "Shipped" | "Delivered" | "Cancelled";
  itemCount: number;
}

const initialOrders: Order[] = [
  { id: "ORD001", customerName: "Alice Johnson", customerEmail: "alice.johnson@example.com", orderDate: "2024-07-15", totalAmount: 120.50, status: "Delivered", itemCount: 3 },
  { id: "ORD002", customerName: "Bob Williams", customerEmail: "bob.williams@example.com", orderDate: "2024-07-16", totalAmount: 75.00, status: "Shipped", itemCount: 1 },
  { id: "ORD003", customerName: "Carol Davis", customerEmail: "carol.davis@example.com", orderDate: "2024-07-16", totalAmount: 210.00, status: "Pending", itemCount: 5 },
  { id: "ORD004", customerName: "David Miller", customerEmail: "david.miller@example.com", orderDate: "2024-07-17", totalAmount: 45.99, status: "Cancelled", itemCount: 2 },
  { id: "ORD005", customerName: "Eve Brown", customerEmail: "eve.brown@example.com", orderDate: "2024-07-18", totalAmount: 88.25, status: "Delivered", itemCount: 4 },
  { id: "ORD006", customerName: "Frank Wilson", customerEmail: "frank.wilson@example.com", orderDate: "2024-07-18", totalAmount: 150.00, status: "Pending", itemCount: 2 },
  { id: "ORD007", customerName: "Grace Lee", customerEmail: "grace.lee@example.com", orderDate: "2024-07-19", totalAmount: 60.70, status: "Shipped", itemCount: 1 },
  { id: "ORD008", customerName: "Henry Garcia", customerEmail: "henry.garcia@example.com", orderDate: "2024-07-20", totalAmount: 300.00, status: "Delivered", itemCount: 6 },
  { id: "ORD009", customerName: "Ivy Rodriguez", customerEmail: "ivy.rodriguez@example.com", orderDate: "2024-07-20", totalAmount: 22.50, status: "Cancelled", itemCount: 1 },
  { id: "ORD010", customerName: "Jack Martinez", customerEmail: "jack.martinez@example.com", orderDate: "2024-07-21", totalAmount: 199.99, status: "Pending", itemCount: 3 },
];

type SortKey = keyof Order | null;
type SortDirection = "ascending" | "descending";

interface SortConfig {
  key: SortKey;
  direction: SortDirection;
}

interface ColumnConfig {
  id: keyof Order | 'actions';
  label: string;
  canHide: boolean;
  sortable: boolean;
  minWidth?: string;
  className?: string;
  headerClassName?: string;
}

const columnsConfig: ColumnConfig[] = [
  { id: 'id', label: 'Order ID', canHide: false, sortable: true, minWidth: "120px" },
  { id: 'customerName', label: 'Customer', canHide: true, sortable: true, minWidth: "180px" },
  { id: 'orderDate', label: 'Date', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'totalAmount', label: 'Total', canHide: true, sortable: true, className: "text-right", minWidth: "100px" },
  { id: 'status', label: 'Status', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'itemCount', label: 'Items', canHide: true, sortable: true, className: "text-center", minWidth: "80px" },
  { id: 'actions', label: 'Actions', canHide: false, sortable: false, className: "text-right", minWidth: "130px" },
];

const ORDER_STATUSES: Order["status"][] = ["Pending", "Shipped", "Delivered", "Cancelled"];

function OrdersPageContent() {
  const [orders, setOrders] = useState<Order[]>(initialOrders);
  const [sortConfig, setSortConfig] = useState<SortConfig>({ key: null, direction: 'ascending' });

  const initialVisibility = columnsConfig.reduce((acc, col) => {
    acc[col.id as string] = true;
    return acc;
  }, {} as Record<string, boolean>);
  const [columnVisibility, setColumnVisibility] = useState<Record<string, boolean>>(initialVisibility);

  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState<"All" | Order["status"]>("All");
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageInput, setPageInput] = useState(currentPage.toString());

  const [showCancelConfirm, setShowCancelConfirm] = useState(false);
  const [orderToCancel, setOrderToCancel] = useState<Order | null>(null);

  useEffect(() => {
    setPageInput(currentPage.toString());
  }, [currentPage]);

  const handlePageInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPageInput(e.target.value);
  };

  const handlePageInputSubmit = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      const newPage = parseInt(pageInput, 10);
      if (!isNaN(newPage) && newPage >= 1 && newPage <= totalPages) {
        setCurrentPage(newPage);
      } else {
        setPageInput(currentPage.toString());
      }
    }
  };

  const filteredOrders = useMemo(() => {
    return orders.filter(order => {
      const searchLower = searchTerm.toLowerCase();
      const matchesSearchTerm =
        order.id.toLowerCase().includes(searchLower) ||
        order.customerName.toLowerCase().includes(searchLower);
      const matchesStatus =
        statusFilter === 'All' || order.status === statusFilter;
      return matchesSearchTerm && matchesStatus;
    });
  }, [orders, searchTerm, statusFilter]);

  const sortedOrders = useMemo(() => {
    let sortableItems = [...filteredOrders];
    if (sortConfig.key !== null) {
      sortableItems.sort((a, b) => {
        const aVal = a[sortConfig.key!];
        const bVal = b[sortConfig.key!];

        if (typeof aVal === 'number' && typeof bVal === 'number') {
          return sortConfig.direction === 'ascending' ? aVal - bVal : bVal - aVal;
        }
        if (typeof aVal === 'string' && typeof bVal === 'string') {
          return sortConfig.direction === 'ascending' ? aVal.localeCompare(bVal) : bVal.localeCompare(aVal);
        }
        return 0;
      });
    }
    return sortableItems;
  }, [filteredOrders, sortConfig]);

  const totalPages = useMemo(() => {
    return Math.max(1, Math.ceil(sortedOrders.length / rowsPerPage));
  }, [sortedOrders.length, rowsPerPage]);

  const paginatedOrders = useMemo(() => {
    const startIndex = (currentPage - 1) * rowsPerPage;
    const endIndex = startIndex + rowsPerPage;
    return sortedOrders.slice(startIndex, endIndex);
  }, [sortedOrders, currentPage, rowsPerPage]);


  const requestSort = (key: SortKey) => {
    if (!key) return;
    let direction: SortDirection = 'ascending';
    if (sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    setSortConfig({ key, direction });
  };

  const getSortIcon = (key: SortKey) => {
    if (sortConfig.key !== key) {
      return <ArrowUpDown className="ml-2 h-4 w-4 opacity-50" />;
    }
    return sortConfig.direction === 'ascending' ?
      <ArrowUpDown className="ml-2 h-4 w-4 text-primary" /> :
      <ArrowUpDown className="ml-2 h-4 w-4 text-primary" />;
  };

  const toggleColumnVisibility = (columnId: string) => {
    setColumnVisibility(prev => ({ ...prev, [columnId]: !prev[columnId] }));
  };

  const handleCancelOrderClick = (order: Order) => {
    setOrderToCancel(order);
    setShowCancelConfirm(true);
  };

  const handleConfirmCancel = () => {
    if (orderToCancel) {
      setOrders(prevOrders =>
        prevOrders.map(o =>
          o.id === orderToCancel.id ? { ...o, status: "Cancelled" } : o
        )
      );
    }
    setShowCancelConfirm(false);
    setOrderToCancel(null);
  };

  const handleViewOrder = (order: Order) => {
    console.log("Viewing order:", order.id);
  };

  const handleUpdateOrderStatus = (order: Order) => {
    console.log("Updating status for order:", order.id);
  };

  const getStatusBadgeVariant = (status: Order["status"]): "default" | "secondary" | "destructive" | "outline" => {
    switch (status) {
      case "Pending":
        return "default"; 
      case "Shipped":
        return "secondary"; 
      case "Delivered":
        return "outline"; 
      case "Cancelled":
        return "destructive";
      default:
        return "outline";
    }
  };


  return (
    <div className="space-y-8">
      <Card className="shadow-md w-full mx-auto">
        <CardHeader className="p-2.5">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-x-4 gap-y-2">
            <CardTitle className="whitespace-nowrap flex-shrink-0 flex items-center">
              <ShoppingCart className="mr-2 h-5 w-5 text-primary" /> My Orders
            </CardTitle>

            <div className="flex flex-1 min-w-0 items-center gap-2">
              <div className="relative flex-grow">
                <SearchIcon className="absolute left-2.5 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search Order ID or Customer..."
                  value={searchTerm}
                  onChange={(e) => { setSearchTerm(e.target.value); setCurrentPage(1); }}
                  className="pl-8 w-full"
                />
              </div>

              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="outline" size="icon" className="flex-shrink-0">
                    <Eye className="h-4 w-4" />
                    <span className="sr-only">Toggle Columns</span>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuLabel>Toggle Columns</DropdownMenuLabel>
                  <DropdownMenuSeparator />
                  {columnsConfig.filter(col => col.canHide).map((column) => (
                    <DropdownMenuCheckboxItem
                      key={column.id}
                      checked={columnVisibility[column.id as string]}
                      onCheckedChange={() => toggleColumnVisibility(column.id as string)}
                    >
                      {column.label}
                    </DropdownMenuCheckboxItem>
                  ))}
                </DropdownMenuContent>
              </DropdownMenu>

              <Select value={statusFilter} onValueChange={(value) => { setStatusFilter(value as "All" | Order["status"]); setCurrentPage(1); }}>
                <SelectTrigger className="w-auto min-w-[40px] flex-shrink-0 p-2.5">
                  <FilterIcon className="h-4 w-4 text-muted-foreground" />
                  <span className="sr-only"><SelectValue placeholder="Filter by Status" /></span>
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="All">All Statuses</SelectItem>
                  {ORDER_STATUSES.map(status => (
                    <SelectItem key={status} value={status}>{status}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>
          <CardDescription className="mt-2">
            View and manage your orders. ({filteredOrders.length} orders found)
          </CardDescription>
        </CardHeader>
        <CardContent className="p-0 overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                {columnsConfig.map((column) => (
                  columnVisibility[column.id as string] && (
                    <TableHead
                      key={column.id}
                      style={column.minWidth ? { minWidth: column.minWidth } : {}}
                      className={cn("text-center", column.headerClassName)}
                      onClick={() => column.sortable && requestSort(column.id as keyof Order)}
                    >
                      <div className={cn("flex items-center justify-center", column.sortable ? 'cursor-pointer hover:text-foreground' : '')}>
                        <span className="whitespace-nowrap">{column.label}</span>
                        {column.sortable && getSortIcon(column.id as keyof Order)}
                      </div>
                    </TableHead>
                  )
                ))}
              </TableRow>
            </TableHeader>
            <TableBody>
              {paginatedOrders.map((order) => (
                <TableRow key={order.id}>
                  {columnVisibility['id'] && <TableCell className="font-medium text-center">{order.id}</TableCell>}
                  {columnVisibility['customerName'] && (
                    <TableCell>
                      <div className="flex items-center gap-2">
                        <Avatar className="h-8 w-8">
                          <AvatarImage src={`https://avatar.vercel.sh/${order.customerEmail}.png?size=32`} alt={order.customerName} data-ai-hint="person avatar"/>
                          <AvatarFallback>{order.customerName.split(' ').map(n=>n[0]).join('').toUpperCase().slice(0,2)}</AvatarFallback>
                        </Avatar>
                        <span>{order.customerName}</span>
                      </div>
                    </TableCell>
                  )}
                  {columnVisibility['orderDate'] && <TableCell className="text-muted-foreground text-center">{order.orderDate}</TableCell>}
                  {columnVisibility['totalAmount'] && <TableCell className="text-right">${order.totalAmount.toFixed(2)}</TableCell>}
                  {columnVisibility['status'] && (
                    <TableCell className="text-center">
                      <Badge variant={getStatusBadgeVariant(order.status)}>
                        {order.status}
                      </Badge>
                    </TableCell>
                  )}
                  {columnVisibility['itemCount'] && <TableCell className="text-center">{order.itemCount}</TableCell>}
                  {columnVisibility['actions'] && (
                    <TableCell className="text-right">
                       <div className="flex items-center justify-end gap-1">
                        <Button
                          size="icon"
                          onClick={() => handleViewOrder(order)}
                          aria-label="View order"
                          className="bg-green-500 hover:bg-green-600 text-white"
                        >
                          <Eye className="h-4 w-4" />
                        </Button>
                        <Button
                          size="icon"
                          onClick={() => handleUpdateOrderStatus(order)}
                          aria-label="Update order status"
                           className="bg-yellow-500 hover:bg-yellow-600 text-black"
                        >
                          <PencilLine className="h-4 w-4" />
                        </Button>
                        {order.status !== "Cancelled" && order.status !== "Delivered" && (
                          <Button
                            size="icon"
                            onClick={() => handleCancelOrderClick(order)}
                            aria-label="Cancel order"
                            className="bg-red-500 hover:bg-red-600 text-white"
                          >
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        )}
                      </div>
                    </TableCell>
                  )}
                </TableRow>
              ))}
            </TableBody>
          </Table>
          {paginatedOrders.length === 0 && (
             <div className="border border-dashed border-border rounded-md p-8 text-center mt-4">
                <p className="text-muted-foreground">No orders found matching your criteria.</p>
            </div>
          )}
        </CardContent>
        <CardFooter className="flex flex-row items-center justify-between gap-4 border-t p-2.5">
          <div className="flex flex-col items-start text-sm text-muted-foreground space-y-1">
            <span className="text-xs">Rows</span>
            <Select
              value={rowsPerPage.toString()}
              onValueChange={(value) => {
                setRowsPerPage(Number(value));
                setCurrentPage(1);
              }}
            >
              <SelectTrigger className="w-[70px]">
                <SelectValue placeholder={rowsPerPage.toString()} />
              </SelectTrigger>
              <SelectContent>
                {[5, 10, 15, 20].map((pageSize) => (
                  <SelectItem key={pageSize} value={pageSize.toString()}>
                    {pageSize}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <div className="flex flex-col items-end space-y-1">
            <span className="text-xs text-muted-foreground">
              Page {totalPages === 0 ? 0 : currentPage} of {totalPages}
            </span>
            <div className="flex items-center space-x-1">
              <Button
                variant="outline"
                size="icon"
                onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
                disabled={currentPage === 1 || totalPages === 0}
                aria-label="Go to previous page"
              >
                <ChevronLeft className="h-4 w-4" />
              </Button>
              <Input
                type="number"
                value={pageInput}
                onChange={handlePageInputChange}
                onKeyDown={handlePageInputSubmit}
                className="h-9 w-14 text-center px-1"
                min="1"
                max={totalPages}
                disabled={totalPages === 0}
                aria-label="Page number input"
              />
              <Button
                variant="outline"
                size="icon"
                onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
                disabled={currentPage === totalPages || totalPages === 0}
                aria-label="Go to next page"
              >
                <ChevronRight className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </CardFooter>
      </Card>

      <AlertDialog open={showCancelConfirm} onOpenChange={setShowCancelConfirm}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Are you sure you want to cancel this order?</AlertDialogTitle>
            <AlertDialogDescription>
              This action will mark order "{orderToCancel?.id}" as Cancelled.
              This may not be reversible depending on system configuration.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => setOrderToCancel(null)}>Keep Order</AlertDialogCancel>
            <AlertDialogAction onClick={handleConfirmCancel} className="bg-destructive hover:bg-destructive/90">
              Cancel Order
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
}

export default function OrdersPage() {
  return (
    <OrdersPageContent />
  );
}

    